import React from 'react';
import { connect } from 'react-redux';
import { loadUsers } from '../actions/UserActions.js';
import { loadPosts, likeUnkikePost } from '../actions/PostActions.js';
var randomUser = require('../assets/img/users/randomUser.png');
const blank = require('../assets/img/like-blank.png')
const liked = require('../assets/img/like-active.png')

class Like extends React.Component {
    _isMounted = false;
    state = {
        isLiked: false,
        likeImgSrc: blank
    }
    componentDidMount() {
        console.log('like mount')
        this._isMounted = true;
        if (this._isMounted) {
            const { post, user } = this.props
            this.checkLikeState(post, user)
        }
    }
    componentWillUnmount() {
        // console.log('unmount')
        this._isMounted = false;
        //const { post, user } = this.props
        //this.checkLikeState(post, user)
        // this.setState({ isLiked: false, likeImgSrc: blank })
    }
    onLikeClicked = async (post, user, isLiked) => {
        console.log('clicked postId: ' + user._id)
        console.log('clicked userId: ' + user._id)
        if (!isLiked) {//like post
            //console.log('false')
            try {
                await this.props.likeUnkikePost(post, user, isLiked)
                    .then((res) => {
                        this.setState({ isLiked: true, likeImgSrc: liked })
                    })
            } catch (err) {
                console.log('onLikeClicked -> error in likePost: ', err)
            }
        } else if (isLiked) {//unlike post
            //console.log('true');
            try {
                await this.props.likeUnkikePost(post, user, isLiked)
                    .then((res) => {
                        this.setState({ isLiked: false, likeImgSrc: blank })
                    })
            } catch (err) {
                console.log('onLikeClicked -> error in likePost: ', err)
            }
        }
    }

    checkLikeState = (post, user) => {
        // console.log('like checkLikeState postId: ' + this.state.post._id)
        // console.log('like checkLikeState userId: ' + this.state.user._id)
        // var isNotFound = true;
        post.likedBy.forEach(liker => {
            if (liker._id === user._id) {
                // isNotFound = false;
                this.setState({ isLiked: true, likeImgSrc: liked })
            }
        });
        // if (isNotFound) {
        //     console.log('not found')
        //     this.setState({ isLiked: false, likeImgSrc: blank })
        // }
    }

    render() {
        const { btnClassName, post, user } = this.props
        const { isLiked, likeImgSrc } = this.state
        return (
            <img className={btnClassName} alt="like" title="like" onLoad={() => this.checkLikeState(post, user)}
                onClick={() => this.onLikeClicked(post, user, isLiked)} src={likeImgSrc}>
            </img>
        );
    }
}
const mapStateToProps = state => {
    return {
        users: state.user.users,
        loggedInUser: state.user.loggedInUser,
        posts: state.post.posts
    };
};
const mapDispatchToProps = {
    loadUsers,
    loadPosts, likeUnkikePost
};
export default connect(mapStateToProps, mapDispatchToProps)(Like);